<template>
    <div>
        <headtile></headtile>
        <!--  -->
        <div class="row" id="content">
            <div class="col">
            <table class="table ">
                <tr>
                    <td></td>
                    <td>{{detail.student_name}}</td>
                    <td></td>
                    <td>{{detail.gender==0?"":""}}</td>
                    <td rowspan="3">
                        <img class="w-100" :src="`http://smstest.applinzi.com/${detail.avatar}`" alt="">
                    </td>
                </tr>
                <tr>
                    <td></td>
                    <td>{{sid}}</td>
                    <td></td>
                    <td>{{detail.nation}}</td>
                </tr>
                <tr>
                    <td></td>
                    <td>{{detail.Political_status}}</td>
                    <td></td>
                    <td>{{detail.s_phone}}</td>
                </tr>
                <tr>
                    <td></td>
                    <td>{{detail.birthday}}</td>
                    <td></td>
                    <td colspan="2">{{detail.nationality}}</td>
                </tr>
                <tr>
                    <td></td>
                    <td>{{detail.Emergency_contact}}</td>
                    <td></td>
                    <td colspan="2">{{detail.Emergency_contact_phone}}</td>
                </tr>
                <tr>
                    <td></td>
                    <td colspan="4">{{detail.ID_number}}</td>
                </tr>
                <tr>
                    <td></td>
                    <td colspan="4">{{detail.Domicile_place}}</td>
                </tr>
                <tr>
                    <td></td>
                    <td colspan="4">{{detail.address}}</td>
                </tr>
                <tr>
                    <td></td>
                    <td colspan="4">{{detail.message}}</td>
                </tr>

            </table>
            </div>
        </div>
    </div>
</template>
<script>
import Header from './common/Header.vue';
export default {
    data(){
        return{
            sid:20180001,
            detail:""
        }
    },
     components:{
      "headtile":Header
    },
    created() {
        this.sdetail();
    },
    methods: {
        //ajax
        sdetail(){
            var URL=location.href;
            var arr=URL.split("=");
            //sid
            var id=arr[1];
            console.log(id);
            this.sid=id;
            var url="student/detail";
            var obj={sid:this.sid};
            this.axios.get(url,{params:obj})
            .then(result=>{
                console.log(result.data)
                this.detail=result.data[0];
            })
        }
    },
}
</script>
<style scoped>
    #content{
        padding-top:2rem;
        padding-right:2rem;
        padding-left:3rem;
    }
    #content>div{
        padding:2rem;
        background-color:#fff;
    }
    td{
        width:20%;
      border:1px solid #000;  
    }
</style>