<template>
    <div class="container-fluid">
        <!--    header    -->
        <div class="row">
            <!--header -->
            <div class="col-lg-4 col-md-5 col-sm-12 pl-0">
                <div id="header-left" class="row">
                    <!--img logo-->
                    <div @click="goIndex" class="col-10">
                        <img src="../../assets/logo.png" alt="">
                    </div>
                    <!--button  bars-->
                    <div id="bars" class="col-2 mt-2">
                        <div @click="menu">
                            <img src="../../assets/bars.png" alt="">
                        </div>
                        
                    </div>
                </div>
            </div>
             <div class="col-lg-4 col-md-2"></div>
            <!-- header -->
            <div id="header-right" class="col-lg-4 col-md-5 col-sm-12 pr-0">
                    <ul>
                        <!--search -->
                        <li  >
                            <a >
                                <img src="../../assets/search.png" alt="">
                            </a>
                        </li>
                        <!--bear -->
                        <li @mouseenter="enter(1)" @mouseleave="leave">
                            <a>
                                <img src="../../assets/bell.png" alt="">
                                <span class="badge badge-danger">{{iLen}}</span>
                            </a>
                            <ul class="one" v-show="sean&&current==1">
                                <li v-for="(item,index) of iList" :key="index">{{index+1}}.{{item.title}}</li>
                            </ul>
                        </li>
                        <!--envelope -->
                        <li @mouseenter="enter(2)" @mouseleave="leave">
                            <a>
                                <img src="../../assets/envelope.png" alt="">
                                <span class="badge badge-primary">{{mLen}}</span>
                             </a>
                             <ul class="two" v-show="sean&&current==2">
                                <li v-for="(item,index) of mList" :key="index">{{index+1}}.{{item.sender}}</li>
                             </ul>
                        </li>
                        <!---->
                        <li v-show="!islogin">
                            <a href="/#/login"></a> 
                        </li>
                        <li v-show="islogin" @mouseenter="enter(3)" @mouseleave="leave">
                            <a >
                                <img :src="userAvatar" alt="">
                            </a>
                            <ul class="three" v-show="sean&&current==3">
                                <li></li>
                                <li></li>
                                <li></li>
                            </ul>
                        </li>
                    </ul>
            </div>
        </div>
        <asidenav class="row" 
            :isActive="isActive"
        ></asidenav>
    </div>
</template>
<script>
import Aside from './Aside.vue';
export default {
    data(){
        return{
            isActive:false,
            sean:false,
            current:0,
            userAvatar:"",
            iLen:"",     //
            iList:[],   //
            mList:[],   //message
            mLen:"",     //message
            uid:"",
            islogin:false  //falsetrue
        }
    },
    created() {
         this.login()
    },
    methods: {
        //bars
        menu(){
            if(this.isActive==true){
                this.isActive=false;
            }else{
                this.isActive=true;
            }
           
        },

        //
        login(){
            var obj={uid:this.uid}
            this.axios.get("user/userIslogin")
            .then(result=>{
                //console.log(result.data==1)
                if(result.data==1){
                    return;
                }else{
                    this.islogin=true;
                    this.userAvatar="http://127.0.0.1:3000/"+result.data[0].avatar;
                    console
                    .log(result.data[0].avatar)
                }
                 
            })  
            //falsetrue,
                this.imform();
                this.message();
                this.$emit('userIsLogin',(this.islogin));
        },

        //
        enter(index){
            this.sean=true;
            this.current=index;
        },
        //
        leave(){
            this.sean=false;
        },

        //imform(1,2),
        imform(){
            var obj={uid:this.uid}
            this.axios.get("user/inform1")
            .then(result=>{
                this.iLen=result.data.length;
                this.iList=result.data;
                //console.log(this.iLen);
            })  
        },

        //message1,(2),
        message(){
            var obj={uid:this.uid}
            //console.log(this.uid)
            this.axios.get("user/message")
            .then(result=>{
                //console.log(result.data)
                this.mLen=result.data.length;;
                this.mList=result.data;
            })  
        },
        //admin
        goIndex(){
            this.$router.push("/")
        }
    },
    components:{
      "asidenav":Aside
  },
  watch: {
      //
      islogin(){
          //
          this.login()
      }
  },
}
</script>
<style scoped>
    .container-fluid>{
        height:100%;
        
    }
    asidenav{
        height:100%;
    }
    .container-fluid>div:first-child{
         background-color:#fff;
         padding:0rem 2rem;
    }
    .container-fluid>div:last-child{
        height:100%;
    }
    @media screen and (max-width:767px){
   
    }
    /*login */
    #header-left>div:first-child>img{
        width:15rem;
    }
    /* */
    #header-left{
        display:flex;
        justify-content:space-between;
        align-items:center;
    }
    /*headerbars */
    #bars{text-align:end;}
    /*header */
    #header-right{
        display:flex;
        justify-content:flex-end;
        align-items:center;
    }
    /* */
    #header-right>ul>li:last-child>a>img{
        width:2rem;
        border-radius:50%
    }
    .my_small{
        font-size: 0.3rem;
    }
    /*ul */
    #header-right>ul{
        list-style:none;
        display: flex;
        align-items: center;
        margin:0;
    }
    #header-right>ul>li{
        margin:0 1rem;
        position:relative;
    }
    #header-right>ul>li a{
        text-decoration: none;
    }
    /* */
    #header-right>ul>li img{
        width:1.2rem;
    }
    #header-right>ul>li span{
        border-radius: 50%;
        font-weight: 400;
        padding:0.1rem 0.25rem;
        position: absolute;
        bottom:0.4rem;
    }
     /*ul */
    #header-right>ul>li>ul{
        position: absolute;
        z-index:9;
        padding:0.5rem;
        background-color:#fff;
        /*display:none;*/
    }
     #header-right>ul>li>ul>li{
         overflow:auto;
        font-size:12px;
     }
   
    .one{
        width:10rem;
        left:-4rem;
    }
    .two{
        width:10rem;
        left:-4rem; 
    }
    .three{
        width:3rem;
        left:-0.5rem; 
    }
    #header-right ul>li ul>li:hover {
        background-color:#007bff30;
    }
</style>