<template>
    <div>
        <headtile
             @userIsLogin="userIsLogin"
        ></headtile>
        <div class="row">
            <div id="content" class="col mt-2">
                <div class="row m-3 p-3 bg-white">
                    <div class="col-sm-12">
                        <h4></h4>
                        <div class="row mb-2 ">
                            <div class="col-8">
                                 <span></span>
                                <select v-model="count">
                                    <option v-for="(item,index) of arr" :key="index">{{item}}</option>
                                </select>
                            </div>
                            <div class="col-4 w-100">
                                <input v-model="name"  type="text" placeholder="">
                                <span @click="search">
                                    <img src="../assets/search.png">
                                </span>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-sm-12">
                                <table class="table table-striped">
                                    <tr>
                                        <th></th>
                                        <th></th>
                                        <th></th>
                                        <th></th>
                                        <th></th>
                                        <th></th>
                                        <th></th>
                                        <th></th>
                                        <th></th>
                                    </tr>
                                    <tr v-for="(item,index) in list" :key="index">
                                        <!--(pno-1)*count+1-->
                                        <td>{{(pno-1)*count+1+index}}</td>
                                        <td>{{item.sid}}</td>
                                        <td>{{item.student_name}}</td>
                                        <td>{{item.gender==0?"":""}}</td>
                                        <td>{{item.grade}}</td>
                                        <td>{{item.special}}</td>
                                        <td>{{item.Emergency_contact_phone}}</td>
                                        <td>
                                            <a :href="`/#/score?sid=${item.sid}`"></a>
                                        </td>
                                        <td> 
                                            <a :href="`/#/sdetail?sid=${item.sid}`"></a>
                                        </td>
                                    </tr>
                                </table>
                            </div>
                        </div>
                        <pages 
                            :count="count"
                            :totalcount="totalcount"
                            @pnoselect="pnoFromPages"
                        ></pages>
                    </div>
                </div>
            </div> 
        </div>
    </div>
</template>
<script>
import Header from './common/Header.vue';
import pages from './common/pages.vue';
export default {
    data(){
        return{
            list:[],
            arr:[5,10,15,20],
            pno:"1",//
            count:5,//
            totalcount:"",//
            isLogin:false, //
            name:"",  //
        }
    },
     components:{
      "headtile":Header,
      "pages":pages
    },
    created(){
        this.total();
        this.loadList();
        this.userIsLogin();
    },
    computed: {
    },
    methods:{
        //header
        userIsLogin:function(data){
            //console.log("data:"+data)
            this.isLogin=data;
            console.log(data)
        },
        //pno
        pnoFromPages:function(data){
            //console.log(""+data);
            this.pno=data;
        },


        //
        total(){
            //ajax
            var url="student/total";
            this.axios.get(url).then(result=>{
                //console.log(result.data.length);
                this.totalcount=result.data.length;
            })
        },
        //
        loadList(){
            //console.log(111);
             //1this.pno
            //2url 
           if(this.isLogin==false){
               return;
           }else{
                var url="student/StudentList";
                //3
                var obj={pno:this.pno,count:this.count};
                //4ajax
                this.axios.get(url,{params:obj})
                .then(result=>{
                    console.log(result.data[0].student_name);
                    var arr=[]
                    for(var i=0;i<result.data.length;i++){
                        if(result.data[i].student_name.indexOf(this.name)!=-1){//-1
                            arr.push(result.data[i])
                        }
                    }
                    this.list=arr;
                });
            }
            //
            //ajax
        },
        //search
        search(){
            console.log(this.name)
            this.loadList()
        }
    },
    watch:{
        //count
        count(){
            this.loadList();
        },
        pno(){
            this.loadList();
        },
         isLogin(){
             this.loadList();
         }
    }
};
</script>
<style scoped>
    th,td{
        /* border:1px solid #000; */
        text-align:center;
    }
    a{
        text-decoration: none;
    }
    h4{
        text-align:center;
    }
    /**/      
    .table-striped>tr:nth-of-type(odd){
        background-color:#f2f2f2
        }
    /*  */
    .table-striped>tr:hover{background-color:#FAFAD2}

</style>