<template>
    <div class="container-fluid">
        <div class="mt-2">
            <div>
                <img class="w-100" src="../assets/logo.png" alt="">
            </div>
            <div class="mt-2">
                <form class="bg-white" method="post" action="/user/login">
                    <div class="f-color">
                        <strong></strong>
                        <span id="uspan" class="ml-2"></span>
                    </div>
                    <div id="uInput">
                        <input v-model="uname" type="text" class="form-control" placeholder="3">
                    </div>
                    <div>
                        <input v-model="email" type="email" class="form-control" placeholder="">
                    </div>
                    <div>
                        <input v-model="upwd" type="password" class="form-control" placeholder="6-12">
                    </div>

                    <div>
                        <input v-model="upwd2" type="password" class="form-control" placeholder="">
                    </div>
                    <div class="row small">
                        <label @click="checked" class="col-6 f-color" >
                            <input type="checkbox" v-model="checked" :checked="checked">
                        </label>
                        <div>
                            <a href="#">&lt;&lt;&gt;&gt;</a>
                            <a href="#">&lt;&lt;&gt;&gt;</a>
                        </div>

                    </div>
                    <div class="mt-1">
                        <button @click="reg"  class="btn btn-primary w-100" :disabled="disabled"></button>
                    </div>

                    <div class="text-center small">
                        <p class="mb-0 f-color">
                            <a @click="login"  href="/#/Login" class="text-danger"></a>
                        </p>
                    </div>
                </form>
            </div>
        </div>

    </div>
</template>
<script>
export default {
    data(){
        return {
            uname:"",
            upwd:"",
            upwd2:"",
            email:"",
            checked:false,
            disabled:true
        }
    },
    methods: {
        //uname
        search(){
            //this.uname
            //console.log(this.uname)
            //ajax
            var uspan=document.getElementById("uspan");
             uspan.innerHTML="";
            //console.log(uspan)
            this.axios.get("user/exist",{params:{uname:(this.uname)}}).then(result=>{
                console.log(result.data);
                if(result.data==1){
                    uspan.innerHTML="";
                    uspan.style.color="red"
                    console.log("");
                }
            });
            
        },
        //
        checkbox(){
            if(this.checked==true){
                this.disabled=false;
            }else{
                this.disabled=true;
            }
        },   
        reg(e){
            e.preventDefault();
            //console.log(111)
            //unameupwdemailupwd2
            var n=this.uname;
            var p=this.upwd;
            var p2=this.upwd2;
            var e=this.email;
            //
            var regn=/^\w{3,}$/;    //3-12
            var regp=/^\w{6,12}$/;   //6-12
            var rege=/^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
            if(!regn.test(n)){
                this.$toast("3");
                return;
            }else if(!rege.test(e)){
                 this.$toast("");
                return;
            }else if(!regn.test(p)){
                 this.$toast("6-12");
                return;
            };
            //upwdypwd2
            if(p!=p2){
                this.$toast("");
                return;
            };
            console.log(222);
            //ajax
            var url="user/reg";
            var obj={uname:n,upwd:p,email:e};
            this.axios.get(url,{params:obj})
            .then(result=>{
                console.log("ok");
                this.$router.push("Login");
            })
        },
        login(){
            this.$router.push("./Login")
        }
    },
    //
    watch:{
        uname(){
            this.search();
        },
        checked(){
            this.checkbox();
        }
    }
}
</script>
<style scoped>
    .container-fluid{
        display:flex;
        align-items:center;
        height:100%;
        background-color:gray;
}
form{
    padding: 2rem;
}
form>div+div{
    margin-top:1.5rem;
}
.small{
    font-size:0.8rem;
}

/*@media screen and (min-width:992px){
    .container-fluid>div{
        width:50%;
        margin:0 auto;
    }
}
@media screen and (max-width:991px) and (min-width:768px){
    .container-fluid>div{
        width:50%;
        margin:0 auto;
    }
}
@media screen and (max-width:767px){
    .container-fluid>div{
        width:75%;
        margin:0 auto;
    }
}*/
.container-fluid>div{
    width:35rem;
    min-width:35rem;
    margin:0 auto;
}
a:hover{
    text-decoration: none;
}
.f-color{
    color:#878787;
    text-decoration: none;
}
</style>