<template>
    <div>
        <headtile></headtile>
        <div class="row">
            <div id="content" class="col mt-2">
                <div class="row m-3 p-3 bg-white">
                    <div class="col-sm-12">
                        <h4></h4>
                        <div class="mb-2">
                            <span></span>
                            <select v-model="count">
                                <option v-for="(item,index) of arr" :key="index">{{item}}</option>
                            </select>
                        </div>
                        <div class="row">
                            <div class="col-sm-12">
                                <table class="table table-striped">
                                    <tr>
                                        <th></th>
                                        <th></th>
                                        <th></th>
                                        <th></th>
                                        <th></th>
                                    </tr>
                                    <tr v-for="(item,index) in list" :key="index">
                                        <!--(pno-1)*count+1-->
                                        <td>{{(pno-1)*count+1+index}}</td>
                                        <td>{{item.gid}}</td>
                                        <td>{{item.status==1?"":""}}</td>
                                        <td>{{item.comment}}</td>
                                        <td>
                                            <a></a>
                                        </td>
                                    </tr>
                                </table>
                            </div>
                        </div>
                        <pages 
                            :count="count"
                            :totalcount="totalcount"
                            @pnoselect="pnoFromPages"
                        ></pages>
                    </div>
                </div>
            </div> 
        </div>
    </div>
</template>
<script>
import Header from './common/Header.vue';
import pages from './common/pages.vue';
export default {
    data(){
        return{
            list:[],
            arr:[5,10,15,20],
            pno:"1",//
            count:5,//
            totalcount:""//
        }
    },
     components:{
      "headtile":Header,
      "pages":pages
    },
    created(){
        this.total();
        this.loadList();

    },
    computed: {
    },
    methods:{
        pnoFromPages:function(data){
            //console.log(""+data);
            this.pno=data;
        },
        //
        total(){
            //ajax
            var url="student/gradeTotal";
            this.axios.get(url).then(result=>{
                //console.log(result.data.length);
                this.totalcount=result.data.length;
            })
        },
        //
        loadList(){
            //console.log(111);
             //1this.pno
            //2url 
            var url="/student/grade";
            //3
            var obj={pno:this.pno,count:this.count};
            //4ajax
            this.axios.get(url,{params:obj})
            .then(result=>{
                //console.log(result.data);
                this.list=result.data;
            });
            //
            //ajax
        }
    },
    watch:{
        //count
        count(){
            this.loadList();
        },
        pno(){
            this.loadList();
        }
    }
};
</script>
<style scoped>
    th,td{
        /* border:1px solid #000; */
        text-align:center;
    }
    a{
        text-decoration: none;
    }
    h4{
        text-align:center;
    }
    /**/      
    .table-striped>tr:nth-of-type(odd){
        background-color:#f2f2f2
        }
    /*  */
    .table-striped>tr:hover{background-color:#FAFAD2}

</style>